import React, { Component } from "react";
import Eggplant from "./contracts/Eggplant.json";
import getWeb3 from "./getWeb3";
import Box from '@mui/material/Box';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import { FixedSizeList } from 'react-window';
import Moment from 'moment';
import Pdf from './Document.pdf';
import rainingeggplants from './rainingeggplantsplain.svg';
import eggplantlogo from './eggplantlogo.svg';

import "./App.css";

class App extends Component {
  state = { web3: null, account: null, contract: null, donations: [], donationAmount: 0, nextAirdrop: 0 };

  componentDidMount = async () => {
   document.title = "Eggplant Token"

    this.submitDonation = this.submitDonation.bind(this);
    this.airdrop = this.airdrop.bind(this);
    this.changeDonation = this.changeDonation.bind(this);
    
    try {
      // Get network provider and web3 contract.
      const Web3 = require('web3');
      const web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));


      //move to the connect section
      // Use web3 to get the user's accounts.
//      const accounts = await web3.eth.getAccounts();

      // Get the contract.
      const contract = new web3.eth.Contract(
        Eggplant.abi,
        '0xC334358Bcf0e2cf3E607da0F51bCb4052A434933',
      );

      // Set web3, accounts, and cl trac to the state, and then proceed with an
      // example of interacting with the cl trac's methods.
      this.setState({ web3, contract}, this.initSiteState);
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`,
      );
      console.error(error);
    }
  };

  renderRow({ data, index, style }) {

    const item = data[index];
    const date =  Moment.unix(item.date).format("YYYY-MM-DD");
    const adjustedAmount = item.amount / (10 ** 18);

    return (
      <ListItem style={{'margin-top': '8px', 'margin-bottom':'16px'}} key={index} component="div" disablePadding>
          <ListItemText primary={<div><span style={{'margin-left': '16px', 'color':'#50fa7b', 'margin-right': '18px'}}>Donor</span>{item.donor}<br/> <span style={{'margin-left': '16px', 'color':'#50fa7b', 'margin-right': '2px'}}>Amount</span> {adjustedAmount} BNB<br/><span style={{'margin-left': '16px', 'color':'#50fa7b', 'margin-right': '25px'}}>Date</span> {date}<hr style={{color:'#50fa7b'}}/></div>} />
      </ListItem>
    );
  }

  changeDonation(event) {    this.setState({donationAmount: event.target.value});  }


  async refreshDonations() {
      const { contract } = this.state;
      const donations = await contract.methods.getDonations().call();
      if (donations != null) {
        this.setState({donations: donations});
      }
  }

  async submitDonation(event) {
    event.preventDefault();
    const { accounts, contract, donationAmount } = this.state;
    await contract.methods.donate().send({from: accounts[0], value: donationAmount * 10 ** 18 });
    this.refreshDonations()
  }

  async refreshNextAirdrop() {
      const { contract } = this.state;
      const nextAirdrop = await contract.methods.getNextAirdrop().call() / (10 ** 18);
      this.setState({nextAirdrop: nextAirdrop})
  }

  async airdrop() {
      const { accounts, contract } = this.state;

      const result = await contract.methods.checkAirdropAvailability({from: accounts[0]}).call();
      if (result == 0) {
          alert("Only one airdrop per user is allowed.")
      } else if (result == 1) {
          alert("Airdrop supply is empty.")
      } else {
           await contract.methods.airdrop().send({from: accounts[0]});
          this.refreshNextAirdrop();
      }
  }

  initSiteState = async () => {
    this.refreshDonations();
    this.refreshNextAirdrop();
  };

  render() {
    return (
      <div className="App">
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>

        <h1>Eggplant Token <img src={eggplantlogo}/></h1>

        <p>Eggplant Token <img src={eggplantlogo}/> (<span style={{'color':'#50fa7b'}}>EPT</span>) is a BEP-20 meme
        token with <span style={{'color':'#50fa7b'}}>zero token fees</span>. It has become all too common for meme tokens
        to include additional fees to those imposed by the BSC network and the team behind EPT finds that unfair. In
        a bid to maximise user satisfaction EPT also introduces renewed tokenomics like
        <span style={{'color':'#50fa7b'}}> self-service airdrops</span>. These and the rest of EPT's tokenomics can
        be read from the <a href = {Pdf} rel="noopener noreferrer" target = "_blank" style={{'color':'#50fa7b', 'text-decoration': 'none'}}>whitepaper <i class="fa fa-file-pdf-o"></i></a>.</p>






        <h2>Self-service Airdrops</h2>


<section class="container">
  <div class="left-half">

  </div>
  <div class="right-half">
  </div>
</section>



<section class="container">
  <div class="left-half">
      <p>EPT has a dedicated <span style={{'color':'#50fa7b'}}>airdrop pool of tokens</span> from which first time
               users can acquire a small amount of <span style={{'color':'#50fa7b'}}>free</span> EPT.</p>
               <p>Each subsequent user gets a smaller amount of airdropped tokens until the airdrop pool is emptied. The
               size of the airdrop pool and the airdrop payout scheme are detailed in the whitepaper.</p>

              <p>Next airdrop is <span style={{'color':'#50fa7b'}}>{this.state.nextAirdrop} EPT</span>
              <button class="button2" onClick={this.airdrop}>Claim</button></p>
  </div>
  <div class="right-half"><img src={rainingeggplants}/></div>
</section>





        <h2>Supporting the developers</h2>
        <section class="container">
          <div class="left-half">
        <p>Creating and maintaining tokens is hard work. To maintain token communities alive devs often include fees in their tokenomics. By removing all token fees, users can be sure what they pay is what they get. However, this removes the main source of funding.
          .If you'd like to support the developers you may consider donating. All donations are highly appreciated and will be used to support the development of future projects. Suggestions for improvements are also welcome (see bottom of site for contact details).</p>
        <p>Donations via this site are audited in the Donation Box to provide transparency to our users.</p>
      <form onSubmit={this.submitDonation}>
     	<span style={{'color':'#50fa7b'}}>Amount (BNB)</span> <input type="number" step="any" min='0.00001' required onChange={this.changeDonation.bind(this)}/>
  	<input type="submit" class="button1" value="Donate"/>
      </form>
           </div>
            <div class="right-half">

      <Box sx={{width: '100%', height: 320, maxWidth: 517, bgcolor: 'transparent', border: '2px solid #50fa7b', 'border-radius': '6px' }}>
            <FixedSizeList
              height={320}
              width={517}
              itemData={this.state.donations.reverse()}
              itemSize={100}
              itemCount={this.state.donations.length}>
              {this.renderRow}
            </FixedSizeList>
          </Box>
       </div>
     </section>
      <h3>About</h3>
      <div style={{'font-size': '90%', 'line-height': '0.9'}}>
      <p><a style={{color: '#f8f8f2', 'text-decoration': 'none'}} href="mailto:devedcrafts@protonmail.com"><i class="fa fa-envelope" style={{'color':'#50fa7b'}}></i> devedcrafts</a></p>
      <p><a style={{color: '#f8f8f2', 'text-decoration': 'none'}} href="https://t.me/eggplant_token" target="_blank" rel="noopener noreferrer"><i class="fa fa-telegram" style={{'color':'#50fa7b', 'font-size':'20px'}}></i> eggplant_token</a></p>
      </div>
      </div>
    );
  }
}

export default App;
